import getEnvironment, { environments } from '~global/js/helpers/environment';
import getCdnBasePath from '~global/js/helpers/cdn';

const apiBasePath = getEnvironment() === environments.DEV ? 'http://localhost:8090' : '';

export const paths = {
  ajax: `${apiBasePath}/microapp-search-results-page`,
  mapboxScript: `${getCdnBasePath()}map/vendor/mapbox.vendor.js`,
  leafletStyles: `${getCdnBasePath()}map/vendor/leaflet.vendor.css`,
};

export const mapboxGLPaths = {
  mapboxGLScript: `${getCdnBasePath()}map/vendor/mapbox-gl.dc3ec66e546f427f39b492a1c7306316.js`,
  geocoderScript: `${getCdnBasePath()}map/vendor/mapbox-gl-geocoder.e339c7fb27dc48a441d9c8321490e723.js`,
  mapboxGLCss: `${getCdnBasePath()}map/vendor/mapbox-gl.526469114da760a7124dfc1605802971.css`,
  geocoderCss: `${getCdnBasePath()}map/vendor/mapbox-gl-geocoder.12287cbc62cf074fce723866bf318495.css`,
  communRoutes: 'https://api.mapbox.com/directions/v5/mapbox/{type}/{longitude1}%2C{latitude1}%3B{longitude2}%2C{latitude2}.json?access_token=',
};

export const endPoints = {
  propertiesSearch: `${paths.ajax}/property-list`,
  universitiesSearch: `${paths.ajax}/university-list/`,
  clientInfo: '/microapp-home-page/get-client-info',
};

export const shortenSharingPath = `//a.${getEnvironment() === environments.PROD ? 'student' : 'dandythrust'}.com`;



