import cookies from 'js-cookie';
import Clipboard from 'clipboard';
import { isLargeScreen } from '~global-js/helpers/breakpoints';
import generateQRCode from '~global-js/helpers/generateQRCode';
import getEnvironment, { environments } from '~global-js/helpers/environment';
import customEvents from '~global/variables/custom-events.json';
import { getLocale } from '~global-js/helpers/locale';
import axios from '~global-js/helpers/axios';
import { shortenSharingPath } from '~global-js/settings/paths';

export default class ShareWidget {
  constructor(page) {
    this.page = page;
    if (document.querySelector('.partner-sidebar')) {
      this.partnerSidebarContainer = document.querySelector('.partner-sidebar');
      this.partnerButton = document.querySelector('#partner-show');
      this.headerAccount = document.querySelector('.header__subnav--my-account');
      this.partnerShare = this.partnerSidebarContainer.querySelector('.sidebar-share');
      this.scrollLength = 0;
      this.partnerList = this.partnerSidebarContainer.querySelector('.sidebar-list');
      this.partnerListClose = this.partnerSidebarContainer.querySelector('.sidebar-list__item--close');
      this.partnerWechat = this.partnerSidebarContainer.querySelector('.sidebar-list__item--wechat');
      this.partnerLink = this.partnerSidebarContainer.querySelector('.sidebar-list__item--link');
      this.partnerLinkContainer = this.partnerSidebarContainer.querySelector('.sidebar-list__share-link__text');
      this.partnerLinkSuccess = this.partnerSidebarContainer.querySelector('.sidebar-hint');
      this.partnerWechatMask = this.partnerSidebarContainer.querySelector('.sidebar-list__qr-code__mask');
    }
    this.eventBound = false;
  }

  init() {
    if (this.page !== 'studentPortalPage' && this.page !== 'partnerPortalPage'
      && this.partnerSidebarContainer) {
      this.isPartnerEvent();
      this.widgetEvent();
      document.addEventListener(customEvents.partnerCookieSet.name, () => {
        this.isPartnerEvent();
        this.widgetEvent();
      });
      document.addEventListener(customEvents.userLogout.name, () => {
        this.partnerSidebarContainer.classList.remove('partner-sidebar--show');
      });
    }
  }

  isPartnerEvent = () => {
    if (this.getCookie()) {
      this.partnerSidebarContainer.classList.add('partner-sidebar--show');
      this.partnerShare.classList.add('sidebar-share--active');
      this.headerEvent();
      this.updateLink();
    } else {
      this.partnerSidebarContainer.classList.remove('partner-sidebar--show');
    }
  };

  headerEvent = () => {
    if (this.headerAccount) {
      const partner = this.headerAccount.querySelector('.header__subnav-item--my-leads');
      const student = this.headerAccount.querySelectorAll('.header__subnav-item--student');
      const locale = getLocale() === 'zh-cn' ? getLocale() : 'en-us';
      if (partner && student) {
        const partnerLeads = partner.querySelector('.header__subnav-link');
        partner.classList.add('header__subnav-item--active');
        [].slice.call(student).map((item) => {
          item.classList.add('header__subnav-item--hide');
          return true;
        });
        if (getEnvironment() === environments.PROD && partnerLeads) {
          partnerLeads.setAttribute('href', `//partner.student.com/${locale}`);
        } else if (partnerLeads) {
          partnerLeads.setAttribute('href', `//partner.dandythrust.com/${locale}`);
        }
      }
    }
  };

  widgetEvent = () => {
    if (this.eventBound || !this.getCookie()) {
      return;
    }
    this.eventBound = true;
    this.partnerButton.addEventListener('click', () => {
      this.partnerList.classList.add('active');
      this.partnerButton.classList.add('hidden');
      this.partnerSidebarContainer.classList.add('partner-sidebar--active');
      this.scrollLength = window.scrollY || window.pageYOffset;

      if (!isLargeScreen()) {
        document.body.classList.add('no-scroll');
      }

      this.partnerListClose.addEventListener('click', () => {
        this.closeEvent();
      });
    });
    this.shareHintEvent();
    this.wechatEvent();
    this.linkEvent();
    this.emailEvent();
    this.getCookie();
    this.qrCodeCreate();
  };

  notationOfUrl = () => {
    if (window.window.location.href.includes('?')) {
      return '&';
    }
    return '?';
  };

  updateLink = () => {
    if (this.getCookie()) {
      const partnerLinkUrl = `${window.location.href}${this.notationOfUrl()}partner_referrer=${this.getCookie()}`;

      axios.post(shortenSharingPath, {
        url: partnerLinkUrl,
      }).then((response) => {
        if (response && response.data && response.data.shortUrl) {
          this.partnerLinkContainer.value = response.data.shortUrl;
        }
      }).catch((error) => {
        throw new Error(error.message ? error.message : JSON.stringify(error));
      });

      this.partnerLink.setAttribute('data-clipboard-text',
        `${window.location.href}${this.notationOfUrl()}partner_referrer=${this.getCookie()}`);
    }
  };

  closeEvent = () => {
    this.partnerList.classList.remove('active');
    this.partnerButton.classList.remove('hidden');
    this.partnerSidebarContainer.classList.remove('partner-sidebar--active');
    document.body.classList.remove('no-scroll');
    window.scrollTo(0, this.scrollLength);
    this.partnerWechat.classList.remove('active');
    this.partnerListClose.removeEventListener('click', () => {
      this.closeEvent();
    });
    this.partnerSidebarContainer.removeEventListener('click', () => {
      this.backToList();
    });
  };

  wechatEvent = () => {
    if (isLargeScreen()) {
      this.partnerWechat.addEventListener('mouseenter', () => {
        this.qrCodeCreate();
        this.partnerWechat.classList.add('active');
      });
      this.partnerWechat.addEventListener('mouseleave', () => {
        this.partnerWechat.classList.remove('active');
      });
    } else {
      this.partnerWechat.querySelector('.sidebar-list__item__container').addEventListener('click', this.showWeChatQrCode);
    }
  };

  showWeChatQrCode = () => {
    this.partnerWechat.classList.add('active');

    this.partnerWechat.querySelector('.sidebar-list__detail').addEventListener('click', (e) => {
      e.stopPropagation();
    });

    if (this.partnerWechat.className === 'sidebar-list__item sidebar-list__item--wechat active') {
      this.partnerWechatMask.addEventListener('click', () => {
        this.backToList();
      });
    } else {
      this.partnerWechatMask.removeEventListener('click', () => {
        this.backToList();
      });
    }
  };

  linkEvent = () => {
    const copyLinkButton = this.partnerSidebarContainer.querySelector('.sidebar-list__share-link__button');
    if (isLargeScreen()) {
      this.partnerLink.addEventListener('mouseenter', () => {
        this.partnerLink.classList.add('active');
        this.updateLink();
      });
      this.partnerLink.addEventListener('mouseleave', () => {
        this.partnerLink.classList.remove('active');
      });
      copyLinkButton.addEventListener('click', this.desktopLinkListener);
    } else {
      this.partnerLink.addEventListener('click', this.mobileLinkListener);
    }
  };

  emailEvent = () => {
    const emailContainer = this.partnerSidebarContainer.querySelector('.sidebar-list__item--mail');
    const emailLink = this.partnerSidebarContainer.querySelector('.sidebar-list__item--mail-link');
    if (this.getCookie()) {
      emailContainer.addEventListener('click', () => {
        emailLink.setAttribute('href', `mailto:?&body=${encodeURIComponent(window.location.href)}${encodeURIComponent(this.notationOfUrl())}partner_referrer=${this.getCookie()}`);
      });
    }
  };

  backToList = () => {
    const partnerWechat = this.partnerSidebarContainer.querySelector('.sidebar-list__item--wechat');
    const partnerActive = document.querySelector('.partner-sidebar--active');
    if (partnerWechat && partnerActive) {
      partnerWechat.classList.remove('active');
    }
  };

  desktopLinkListener = () => {
    const desktopClipboard = new Clipboard('.sidebar-list__share-link__button');
    desktopClipboard.on('success', this.clipboardSuccess);
  };

  mobileLinkListener = () => {
    const mobileClipboard = new Clipboard('.sidebar-list__item--link');
    mobileClipboard.on('success', this.clipboardSuccess);
  };

  clipboardSuccess = () => {
    this.closeEvent();
    this.partnerLinkSuccess.classList.add('active');
    const removeSuccessMessage = setInterval(() => {
      clearInterval(removeSuccessMessage);
      this.partnerLinkSuccess.classList.remove('active');
    }, 2000);
  };

  getCookie = () => {
    if (cookies && cookies.get('partner_info')) {
      return cookies.get('partner_info');
    }
    return false;
  };

  qrCodeCreate = () => {
    const qrCodeImage = document.getElementById('partner-qr-code');
    if (this.getCookie() && qrCodeImage) {
      generateQRCode(`${window.location.href}${this.notationOfUrl()}partner_referrer=${this.getCookie()}`, qrCodeImage);
    }
  };

  shareHintEvent = () => {
    window.addEventListener('scroll', () => {
      this.partnerShare.classList.remove('sidebar-share--active');
      this.partnerShare.classList.add('sidebar-share--hide');
    });
  };
}



